import { Grid, Paper, Table, TableContainer, TableHead, TableRow, TableCell, TableBody, withStyles, ButtonGroup, Button } from "@material-ui/core";
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import * as TActions from "../../../../../actions/Tax/tax";
//import * as storeActions from "../../../../../actions/Store/stores";
import AddTaxForm from "./AddTaxForm";
import EditIcon from "@material-ui/icons/Edit";
import DeleteIcon from "@material-ui/icons/Delete";
import { useToasts } from "react-toast-notifications";
//import PeopleOutlineTwoToneIcon from '@material-ui/icons/PeopleOutlineTwoTone';
import PageHeader from "../../../PageHeader";
import CategoryTwoToneIcon from '@material-ui/icons/CategoryTwoTone';
import ConfirmDialog from "../../../controls/ConfirmDialog";
//import Controls from "./Controls";
import NotListedLocation  from '@material-ui/icons/NotListedLocation';


const styles = theme => ({
    root: {
        "& .MuiTableCell-head": {


            fontSize: "0.95rem"
        }
    },
    paper: {
        margin: theme.spacing(-8.9),
        padding: theme.spacing(6)
    },

    grid:{
        paddingLeft: theme.spacing(2)
    }
})



const AddTax = ({ classes, ...props }) => {
    //Toast message
    const { addToast } = useToasts()
    const [currentId, setCurrentId] = useState(0)
    const [confirmDialog, setConfirmDialog] = useState(false) 
    useEffect(() => {
        // props.fetchAllCategory()
       // props.fetchAllStores()
       props.fetchAllTaxes();
    }, [props.taxList])

    const showToast= () =>{
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })
        addToast("Record was deleted succesffuly", { appearance: 'error' })

    }
    const onDelete = id => {
            props.DeleteTax(id, showToast )
            setConfirmDialog(false)

    }

    const openInDialog = () => {
        setConfirmDialog(true)
        
        
        
    }

    return (
         <>
         <PageHeader
                title="Tax Settings"
                //subTitle="Form design with validation"
                icon={<CategoryTwoToneIcon fontSize="large" />}
            />,
         <Paper className={classes.paper} elevation={3}>
           
                    
                    <AddTaxForm {...({ currentId, setCurrentId })} />
           
                    <div className={classes.grid}>
                    <table class="table  .table-responsive{-sm|-md|-lg|-xl|-xxl} table-bordered table-hover table-sm table-striped" >
                        <thead class="thead-dark">
                            <tr>
                               
                                <th scope="col">Tax Id</th>
                                <th scope="col">Tax Name</th>
                                <th scope="col">Rate </th>
                                
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                          
                            {
                                     props.taxList.map((record, index) => {
                                         return ( <tr key = {index} hover>
                                           
                                             <td>{record.taxId}</td>
                                             <td>{record.taxName}</td>
                                             <td>{record.rate}%</td>
                                             
                                             
                                             <td>
                                                 <ButtonGroup variant= "text">
                                                    <Button><EditIcon color="primary"
                                                    onClick={()=>{setCurrentId(record.id)}}/></Button>
                                                    <Button><DeleteIcon color="Secondary"
                                                      onClick={() => {
                                                        openInDialog()
                                                        setConfirmDialog({
                                                            onConfirm: () =>{onDelete(record.id)}
                                                        })
                                                        
                                                        
                                                    }
                                                    }
                                                
                                                    /></Button> 
                                                 </ButtonGroup>
                                             </td>
                                         </tr>

                                         )
                                     })
                                 }
                           
                        </tbody>
                    </table>
                    </div>
               
        </Paper>

        <ConfirmDialog 
            title="Are you sure you want to delete this record?"
            subTitle="This operation can't be reversed"
            confirmDialog={confirmDialog}
            setConfirmDialog = {setConfirmDialog}
            />
         </>
    );
}

//export default staff; 

const mapStateToProps = state => ({
    taxList: state.tax.list
    
})

const mapActionToProps = {
    fetchAllTaxes:TActions.fetchAllTaxes,
    DeleteTax: TActions.DeleteTax,  
}

export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(AddTax));

